
body,
html {
  padding: 0;
  margin: 0;
}
h3 {
  font-size: 14px;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
header {
  background: #42b983;
  color: white;
  float: left;
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 24px;
  padding: 2vh;
  width: 100%;
}
.examples {
  padding: 2vw;
}
.box {
  margin-bottom: 30px;
  padding-bottom: 30px;
  border-bottom: 1px dashed #ebebeb;
}



